<template>
  <BaseLayout wrapperClass="pageNotFound">
    <Breadcrumbs />
    <section class="page-not-found pt-8 pb-20">
      <div class="container">
        <div class="row">
          <div class="col-lg-3">
            <SidebarLinks />
          </div>
          <div class="col-lg-9 not-found-img pb-30 pb-lg-0">
            <span class="h1 d-block text-center mb-30 mb-lg-45 px-30 px-md-0">{{
              $t("404_line_1")
            }}</span>
            <div class="img mb-30 mb-lg-60">
              <img
                src="@/base/assets/not-found.png"
                alt="Not Found"
                width="100%"
                height="100%"
              />
            </div>
            <span class="h3 d-block text-center fw-normal">{{
              $t("404_line_2")
            }}</span>
            <span class="h3 d-block text-center fw-normal">{{
              $t("404_line_3")
            }}</span>
            <Retargeted page="notfound" v-if="hrEnabled" />
          </div>
        </div>
      </div>
    </section>
  </BaseLayout>
</template>

<script setup>
import { computed, onMounted } from "vue";
import { useHead } from "@unhead/vue";
import { useI18n } from "vue-i18n";
import { useStore } from "vuex";
import VueCookies from "vue-cookies";
import BaseLayout from "@/base/core/components/common/BaseLayout";
import Breadcrumbs from "@/base/core/components/Breadcrumbs";
import SidebarLinks from "@/base/core/components/SidebarLinks";
import Retargeted from "@/base/core/components/home/Retargeted.vue";
import config from "@config";

const { t } = useI18n();
const store = useStore();

const hrEnabled = computed(
  () => config.helloRetail && config.helloRetail.enabled
);

// Set up meta information using useHead
useHead({
  title: () => t("404_line_1"),
  meta: [
    { name: "title", content: () => t("404_line_1") },
    { name: "keywords", content: () => t("404_line_1") },
    { name: "description", content: () => t("404_line_1") },
    {
      name: "robots",
      content: () =>
        "nofollow,noindex,noarchive,nositelinkssearchbox,nosnippet,noimageindex",
    },
  ],
});

onMounted(async () => {
  if (config.helloRetail && config.helloRetail.enabled) {
    const hello_retail_id = VueCookies.get("hello_retail_id");
    await store.dispatch("home/loadRetargetedProducts", {
      hello_retail_id,
      page: "notfound",
    });
  }
  const bcrumb = { current: "404", routes: [] };
  store.commit("breadcrumbs/set", bcrumb);
});
</script>
